@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body{
  margin: 0;
  font-family: lato;
  background-color: #121212;
  color: white;
}

.container {
  width: 100%;
  max-width: 1266px;
  margin: auto;
  box-sizing: border-box;
  padding: 0px 24px;
}

.header {
    width: 100%;
    height: 50px;
    background-color: #333333;
    margin: 26px 0px;
}

.header .container{
    position: relative;
    height: 100%;
}

.header .container img{
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
}

.container.grid {
    display: flex;
    flex-wrap: wrap;
    padding: 12px;
    min-height: calc(100vh - 100px);
    position: relative;
}

.grid .loading {

    position: absolute;
    top: 0;
    left: 0;
    margin: auto;
    width: 100%;
    height: 100%;
   
    background-color: rgba(18,18,18, 0.5);

}

.loading p{
    position: -webkit-sticky;
    position: sticky;
    top: 50%;
    text-align: center;
}

.card {
    box-sizing: border-box;
    width: calc(20% - 24px);
    border: 2px solid #64C940;
    margin: 12px;
    background-color: #333333;
    padding: 12px;
    position: relative;
}

.card img {
    width: 100%;
    display: block;
}

.card p.card-info__name {
    margin: 0;
    font-size: 18px;
    font-weight: bold;
}

.card p.card-info__name span {
    font-size: 12px;
    display: block;
    width: 100%;
    font-weight: normal;
}

.card p.card-info__generics{
    margin:0px;
    font-size: 12px;
    font-weight: bold;
}

.card p.card-info__generics span {
    font-weight: normal;
}

.card button.favorite{
    position: absolute;
    top: 12px;
    left: 12px;
    background-color: #1E8FAA;
    color: white;
    border: 1px solid white;
    outline: none;
    cursor: pointer; 
    display: none;
}

.card:hover button.favorite{
    display: block;
}

.card.selected{

    border: 6px solid #1E8FAA;

}

.container.paginator {
    display: flex;
    justify-content: flex-end;
    justify-items: center;
    margin-bottom: 24px;
}

.paginator p, .paginator button{
    font-size: 14px;
    color: white;
}

.paginator p {
    border-left: 1px solid white;
    border-right: 1px solid white;
    margin: 0 4px;
    padding: 0 4px;
}

.paginator button {
    background-color: transparent;
    border: 0px;
    outline: none;
    cursor: pointer;
    padding: 0px;
    width: auto;
    height: 18px;
    box-sizing: content-box;
}

.paginator button:hover{
    border-bottom:  1px solid;
}

.paginator p span{
    color: #64C940;
}

.container.searchBox{
    margin-top: 38px;
    margin-bottom: 12px;
}
.searchBox input{
    border: 0;
    border-bottom: 1px solid white;
    color: white;
    background-color: transparent;
    outline: none;
}

.searchBox button{
    outline: none;
    border: 1px solid white;
    background-color: transparent;
    color: white;
    cursor: pointer;
}

